import React, { useState } from 'react';
import './App.css';
import About from './pages/about';
import Projects from './pages/projects';
import sketch from './pages/sketch';
import P5Wrapper from 'react-p5-wrapper';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  const [active, setActive] = useState("Home");


  return (
    <Router>
      <div className="App">
         <P5Wrapper sketch={sketch} />
        <header className="App-header">
          <Link to="/">
            <h1 className={active=="Home" ? "App-category-title-clicked" : "App-category-title"}
            onClick={()=> setActive("Home")}>
              HOME</h1>
          </Link>
          <Link to="/projects">
          <h1 className={active=="Projects" ? "App-category-title-clicked" : "App-category-title"}
            onClick={()=> setActive("Projects")}>PROJECTS</h1>
          </Link>
          <Link to="/about">
          <h1 className={active=="About" ? "App-category-title-clicked" : "App-category-title"}
            onClick={()=> setActive("About")}>ABOUT</h1>
          </Link>
        </header>
        <div className="App-body">
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/projects">
            <Projects />
          </Route>
          <Route exact path="/">
          <div className="Page-body">
            work in progress
          </div>
          </Route>
        </Switch>
        </div>
        <footer className="App-footer">
            <h1 className="App-category-title">Â© 2021</h1>
        </footer>
      </div>
    </Router>
  );
}
export default App;